/**
 * jw-cell
 * @module components/cell
 * @desc 单元格
 * @param url 跳转地址
 */
export default {
  name: "jwcell",
  props: {
    isLink: {
      type: Boolean,
      default: false
    },
    to: {
      type: [String, Object],
      default: ""
    },
    icon: {
      type: String,
      default: ""
    },
    arrowIcon: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: ""
    }
  },
  computed: {
    href: function href() {
      var _this = this;

      if (this.to && !this.added && this.$router) {
        var resolved = this.$router.match(this.to);
        if (!resolved.matched.length) return this.to;
        this.$nextTick(function () {
          //this.added = true;
          _this.$el.addEventListener("click", _this.click);
        });
        return resolved.fullPath || resolved.path;
      }

      return "javascript:;";
    }
  },
  methods: {
    click: function click($event) {
      $event.preventDefault();
      this.$router.push(this.href);
    }
  }
};